<template>
  <div class="page-main">
    <button id="btn" style="opacity: 0"></button>
    <div class="pswp" style="height: 100vh" tabindex="-1" role="dialog" aria-hidden="true">
        <div class="pswp__bg"></div>
        <div class="pswp__scroll-wrap">
          <div class="pswp__container">
            <div class="pswp__item"></div>
            <div class="pswp__item"></div>
            <div class="pswp__item"></div>
          </div>
          <div class="pswp__ui pswp__ui--hidden">

            <div class="pswp__top-bar">
              <div class="pswp__counter"></div>

              <button class="pswp__button pswp__button--close" title="Close (Esc)"></button>

              <!--
              <button class="pswp__button pswp__button--share" title="Share"></button>
              <button class="pswp__button pswp__button--fs" title="Toggle fullscreen"></button>
              <button class="pswp__button pswp__button--zoom" title="Zoom in/out"></button>
              -->

              <div class="pswp__preloader">
                <div class="pswp__preloader__icn">
                  <div class="pswp__preloader__cut">
                    <div class="pswp__preloader__donut"></div>
                  </div>
                </div>
              </div>
            </div>

            <div class="pswp__share-modal pswp__share-modal--hidden pswp__single-tap">
              <div class="pswp__share-tooltip"></div> 
            </div>

            <button class="pswp__button pswp__button--arrow--left" title="Previous (arrow left)">
            </button>

            <button class="pswp__button pswp__button--arrow--right" title="Next (arrow right)">
            </button>

            <div class="pswp__caption">
              <div class="pswp__caption__center"></div>
            </div>

          </div>

        </div>

    </div>

    <q-toolbar class="toolbar" color="#4468B0">
      <q-icon class="button-effect" :style="{opacity: (layer >= 1) ? 1 : 0}" @click="backCluster" name="chevron_left" />
        <q-toolbar-title @click="logout()" class="title">STARMINE
        </q-toolbar-title>
      <q-icon v-show="!loading" @click="refreshCluster()" class="button-effect refresh-icon" name="refresh" />
      <img v-show="loading" src="../assets/loading2.svg" class="refresh-icon">
    </q-toolbar>

      <div class="cluster" ref="cluster">
          <div class="cluster-inner">
            <canvas class="cluster-canvas" id="cluster-canvas"></canvas>
        </div>
    </div>
    <div v-show="false" class="content-detail">
      <img src="">
    </div>
  </div>
</template>

<script>
import jquery from 'jquery'
import createjs from 'createjs-module'
import '../../node_modules/jquery.panzoom/dist/jquery.panzoom.min.js'

import 'photoswipe/dist/photoswipe.css'
import 'photoswipe/dist/default-skin/default-skin.css'
import PhotoSwipe from 'photoswipe/dist/photoswipe.min.js'
import PhotoSwipeUI from 'photoswipe/dist/photoswipe-ui-default.min.js'

let clusterFieldW = 1500
let clusterFieldH = 1500
let isTouch = false
let selectingItem = false

export default {
  data () {
    return {
      hexagonIndex: 0,
      layer: 0,
      contentId: 0,
      loading: false
    }
  },
  mounted () {
    this.initializeWrap()
    this.load()
  },
  methods: {
    // initiazileWrap
    // wrap初期化処理
    initializeWrap () {
      if (window.wrapMode) {
        setTimeout(() => {
          if (window.StatusBar) {
            window.StatusBar.styleLightContent()
          }
          if (window.navigator && window.navigator.splashscreen) {
            window.navigator.splashscreen.hide()
          }
        }, 2000)
      }
    },
    // load
    // ロード処理
    load () {
      this.loading = true
      this.contentId = (this.$route.query.contentId) ? this.$route.query.contentId * 1 : 0
      this.hexagonIndex = (this.$route.query.hexagonIndex) ? this.$route.query.hexagonIndex * 1 : 0
      this.layer = (this.$route.query.layer) ? this.$route.query.layer * 1 : 0

      this.setCanvas()
    },
    // setCanvas
    // キャンバスの設定
    setCanvas () {
      let $canvas = jquery('<canvas class="cluster-canvas" id="cluster-canvas"></canvas>')
      jquery('.cluster-inner').empty().append($canvas)
      let canvas = $canvas[0]
      canvas.width = clusterFieldW
      canvas.height = clusterFieldW

      let w = canvas.width
      let h = canvas.height
      canvas.width = w * window.devicePixelRatio
      canvas.height = h * window.devicePixelRatio
      canvas.style.width = w + 'px'
      canvas.style.height = h + 'px'

      let $panzoom = jquery('.cluster-inner').panzoom({
        onPan: () => {
          isTouch = false
        },
        onZoom: () => {
          isTouch = false
        }})
      $panzoom.parent().on('mousewheel.focal', (e) => {
        e.preventDefault()
        let delta = e.delta || e.originalEvent.wheelDelta
        let zoomOut = delta ? delta < 0 : e.originalEvent.deltaY > 0
        $panzoom.panzoom('zoom', zoomOut, {
          increment: 0.01,
          animate: false,
          focal: e
        })
        return false
      })
      this.getCluster()
    },
    // getCluster
    // クラスターの取得処理
    getCluster () {
      this.$http.post(
        '/cluster',
        {
          content_id: this.contentId,
          hexagon_index: this.hexagonIndex,
          layer: this.layer
        }
      ).then((response) => {
        console.log('cluster response', response)
        let result = response.data
        if (result.comment === 'No session id') {
          this.dialog.alert('クラスタ取得エラー')
          return
        }

        /*
        if (this.layer === 0) {
          this.setInfinityCluster(result.cluster)
        } else {
          this.setCluster(result.cluster)
        }
        */
        this.setCluster(result.cluster)
      })
    },
    // setCluster
    // クラスターの構築
    setCluster (cluster) {
      let hexSize = this.cluster.getHexSize(window.innerWidth / 5)
      let originX = clusterFieldW / 2 + hexSize.w / 2
      let originY = clusterFieldH / 2 + hexSize.h / 2
      let hexPosition = this.cluster.getPosition(hexSize)
      let that = this

      let stage = new createjs.Stage('cluster-canvas')
      createjs.Touch.enable(stage)
      stage.enableMouseOver()
      stage.scaleX = window.devicePixelRatio
      stage.scaleY = window.devicePixelRatio

      cluster.forEach((value, index) => {
        /*
        jquery('.cluster-canvas').drawPolygon({
          fillStyle: value.content_border_color,
          strokeStyle: value.content_border_color,
          x: hexPosition[index][0] + originX,
          y: hexPosition[index][1] + originY,
          radius: hexSize.h / 2,
          sides: 6,
          rotate: 90
        })
        */
        let marginSize = 1
        let image = new Image()
        image.crossOrigin = 'Anonymous'
        image.addEventListener('load', () => {
          let bitmap = new createjs.Bitmap(image)
          bitmap.x = hexPosition[index][0] + originX + marginSize
          bitmap.y = hexPosition[index][1] + originY + marginSize
          let width = hexSize.w - marginSize * 2
          bitmap.scaleX = width / image.width
          bitmap.scaleY = width / image.width

          bitmap.addEventListener('mousedown', (event) => {
            console.log('start')
            isTouch = true
            bitmap.alpha = 0.7
            stage.update()
            setTimeout(() => {
              bitmap.alpha = 1
              stage.update()
            }, 400)
          })
          bitmap.addEventListener('click', (event) => {
            console.log('end')
            bitmap.alpha = 1
            stage.update()
            if (!isTouch) return
            isTouch = false
            that.selectItem(value)
          })
          stage.addChild(bitmap)
          stage.update()
        }, false)
        image.src = value.content_thumbnail
      })

      jquery('.cluster-inner').panzoom('pan', -(originX - document.querySelector('.cluster').clientWidth / 2) - hexSize.w / 2, -(originY - document.querySelector('.cluster').clientHeight / 2) - hexSize.h / 2)
      this.loading = false
    },
    // setInfinityCluster
    // 無限大クラスターの構築
    setInfinityCluster (cluster) {
      let hexSize = this.cluster.getHexSize(window.innerWidth / 5)
      let originX = clusterFieldW / 2 + hexSize.w / 2
      let originY = clusterFieldH / 2 + hexSize.h / 2
      let hexPosition = this.cluster.getPosition(hexSize)
      let that = this

      /*
      transX:
      (blockW - hexW/2) * XN

      transY:
      (blockH - hexH/2) * YN

      adjustX:
      (blockW * maxWN + (blockW * maxWN -1))/2

      adjustY:
      (blockH * maxYN + (blockH * maxYN -1))/2

      hexX:
      hexX + transX - adjustX

      hexY:
      hexY + transY - adjustY
      */
      let blockMaxXN = 4
      let blockMaxYN = 4
      let blockW = hexSize.w * 30 // dummy
      let blockH = hexSize.h * 30 // dummy
      let blockUseNum = [0, 7, 16] // dummy
      let blockOriginX = originX + (blockW * blockMaxXN + hexSize.w * blockMaxXN) / 2
      let blockOriginY = originY + (blockH * blockMaxYN + hexSize.h * blockMaxYN) / 2

      for (var yN = 0; yN < 4; yN++) {
        let blockTransY = (blockH - hexSize.y / 2) * yN
        for (var xN = 0; xN < 4; xN++) {
          cluster.forEach((value, index) => {
            if (blockUseNum.indexOf(index) < 0) {
              return
            }

            let blockTransX = (blockW - hexSize.w / 2) * xN

            jquery('.cluster-canvas').drawPolygon({
              fillStyle: value.content_border_color,
              strokeStyle: value.content_border_color,
              x: hexPosition[index][0] + blockTransX + blockOriginX,
              y: hexPosition[index][1] + blockTransY + blockOriginY,
              radius: hexSize.h / 2,
              sides: 6,
              rotate: 90
            })

            let marginSize = 1
            let image = new Image()
            image.addEventListener('load', () => {
              jquery('.cluster-canvas').drawImage({
                layer: true,
                source: image,
                x: hexPosition[index][0] + blockTransX + blockOriginX + marginSize,
                y: hexPosition[index][1] + blockTransY + blockOriginY + marginSize,
                width: hexSize.w - marginSize * 2,
                height: hexSize.h - marginSize * 2,
                touchstart: () => {
                  isTouch = true
                },
                touchend: function (layer) {
                  if (!isTouch) return
                  isTouch = false

                  jquery(this).animateLayer(layer, {
                    // rotate: '+=180'
                    opacity: 0.8
                  }, 0)

                  setTimeout(() => {
                    jquery(this).animateLayer(layer, {
                      opacity: 1
                    }, 0)
                  }, 240)

                  that.selectItem(value)
                }
              })
            }, false)
            image.src = value.content_thumbnail
          })
        }
      }

      jquery('.cluster-inner').css({
        left: -(originX - document.querySelector('.cluster').clientWidth / 2),
        top: -(originY - document.querySelector('.cluster').clientHeight / 2)
      })
    },
    // selectItem
    // アイテムの選択
    selectItem (item) {
      if (selectingItem) return
      selectingItem = true
      if (item.content_type === 'keyword') {
        if (this.layer >= 2) {
          this.dialog.alert('3階層以上の移動は禁止されています')
          selectingItem = false
          return
        }
        selectingItem = false
        this.$router.push({name: 'main', query: {hexagonIndex: item.hexagon_index, layer: this.layer + 1, contentId: item.content_id}})
      } else {
        this.$http.get(
          '/content/' + item.content_id
        ).then((response) => {
          let result = response.data.data
          if (result.data_type === 'article') {
            let url = result.url
            console.log(url, result.url)
            selectingItem = false
            window.open(url, '_system', 'location=yes')
          } else if (result.data_type === 'image') {
            var pswpElement = document.querySelectorAll('.pswp')[0]

            var items = [
              {
                src: result.image,
                w: 0,
                h: 0
              }
            ]
            var options = {
              history: false,
              focus: false,
              showAnimationDuration: 0,
              hideAnimationDuration: 0
            }

            if (window.pswpGallery) {
              window.pswpGallery.close()
              delete window.pswpGallery
            }

            window.pswpGallery = new PhotoSwipe(pswpElement, PhotoSwipeUI, items, options)

            let loaded = false
            window.pswpGallery.listen('imageLoadComplete', (index, item) => {
              if (loaded) return
              loaded = true
              console.log(item)
              var img = item.container.children[1]
              item.w = img.naturalWidth
              item.h = img.naturalHeight
              window.pswpGallery.invalidateCurrItems()
              window.pswpGallery.updateSize(true)
            })

            window.pswpGallery.init()
            selectingItem = false
          } else {
            this.dialog.alert('イテレーション2: 記事以外のコンテンツ: ' + JSON.stringify(response))
            selectingItem = false
          }
        })
      }
    },
    // refreshCluster
    // クラスタの再読み込み
    refreshCluster () {
      this.load()
    },
    // backCluster
    // 上位レイヤに戻る
    backCluster () {
      if (this.layer === 0) return
      this.$router.back()
    },
    // logout
    // ログアウト処理を行う
    logout () {
      if (window.debugMode && window.confirm('デバッグコマンド: ログアウトを行いますか?')) {
        this.auth.logout(() => {
          this.$router.push({name: 'welcome'})
        })
      }
    }
  },
  watch: {
    // watch $route
    // パラメータ変動時に再読み込みする
    $route () {
      this.load()
    }
  }
}
</script>

<style>
</style>

<style scoped>

.button-effect:active {
  opacity: 0.5;
}
.toolbar {
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;
  font-size: 30px;
  background: rgba(24,77,163, 0.6);
  padding-top: 20px;
  height: 60px;
}
.toolbar .title {
  font-size: 24px;
}
.toolbar .refresh-icon {
  width: 30px;
}

.cluster {
  position: fixed;
  height: inherit;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.cluster-inner {
  // position: relative;
  width: 1500px;
  height: 1500px;
}
.cluster-inner .cluster-canvas {
  position: absolute;
  top: 0;
  left: 0;
}

.hex {
  // border: 1px solid pink;
  cursor: pointer;
  text-align: center;
  position: absolute;
  display: block;
  background-size: 100%;
  filter: drop-shadow(1px 1px 0 white);
  overflow: hidden;
}
.hex img {
  // display: none;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
}
.hex svg {
  transform: scale(0.874);
  transform-origin: top left;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1000;
}

.pswp {
  height: 100vh;
}
</style>



