<template>
  <div>
    <img class="logo" src="../assets/service-logo.png">
    <p class="welcome-text">Welcome to STARMINE!</p>
    <p class="sub-text" @click="devPage">Facebookと連携して、まずは<br>自分の投稿を振り返りましょう！</p>
    <p @click="facebookCreateUser()" class="facebook-button button-effect">Facebookアカウントで登録</p>
    <p @click="createUserOpen" class="email-button button-effect">メールアドレスで登録</p>
    <p class="login-button button-effect" @click="loginOpen">ログイン</p>

    <transition name="slide-top">
      <div v-if="createUserModal" class="create-user slide-top">
        <img src="../assets/close-button.png" alt="" class="close-button button-effect" @click="createUserClose">
        <div class="login-container">
          <p class="title">アカウント作成</p>
          <p class="validate-text">{{validateText}}　</p>
          <p class="email-label">メールアドレス</p>
          <input placeholder="メールアドレス" v-model="data.email" class="email-input" type="text">
          <p class="password-label">パスワード</p>
          <input placeholder="パスワード" v-model="data.password" class="password-input" type="password">
          <input placeholder="確認用パスワード" v-model="data.confirmPassword" class="password-confirm-input" type="password">
          <p class="password-message">パスワードは4文字以上である必要があります。</p>
          <p @click="createUser" :disabled="!data.email || !data.password || data.password != data.confirmPassword || data.password.length < 4" class="login-button button-effect">アカウントの作成</p>
        </div>
      </div>
    </transition>

    <transition name="slide-top">
      <div v-if="loginModal" class="login slide-top">
        <img src="../assets/close-button.png" alt="" class="close-button button-effect" @click="loginClose">
        <div class="login-container">
          <p class="title">ログイン</p>
          <p class="validate-text">{{validateText}}　</p>
          <p class="email-label">メールアドレス</p>
          <input v-model="data.email" class="email-input" type="text">
          <p class="password-label">パスワード</p>
          <input v-model="data.password" class="password-input" type="password">
          <p @click="login" :disabled="!data.email || !data.password" class="login-button button-effect">ログイン</p>
          <p class="forget-button button-effect">パスワードを忘れましたか？</p>
          <hr class="divider">
          <p @click="facebookLogin()" class="facebook-button button-effect">Facebookでログイン</p>
        </div>
        <div v-show="loading" class="loading">
          <img class="loading-image" src="../assets/loading.svg">
          <p class="loading-text">Now Loading...</p>
        </div>
      </div>
    </transition>

    <div v-show="loading" class="loading">
      <img class="loading-image" src="../assets/loading.svg">
      <p class="loading-text">Now Loading...</p>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      loading: false,
      loginModal: false,
      createUserModal: false,
      data: {
        email: '',
        password: '',
        confirmPassword: ''
      },
      validateText: ''
    }
  },
  mounted () {
    this.initializeWrap()
  },
  methods: {
    // initiazileWrap
    // wrap初期化処理
    initializeWrap () {
      if (window.wrapMode) {
        setTimeout(() => {
          if (window.StatusBar) {
            window.StatusBar.styleLightContent()
            // window.StatusBar.overlaysWebView(true)
          }
          if (window.navigator && window.navigator.splashscreen) {
            window.navigator.splashscreen.hide()
          }
        }, 2000)
      }
    },
    // devPage
    // 開発用のテストページ
    devPage () {
      if (!window.debugMode) return
      this.$router.push({name: 'dev'})
    },
    // facebookCreateUser
    facebookCreateUser () {
      setTimeout(() => {
        this.loading = true
      }, 1000)
      this.auth.facebookCreateUser((data) => {
        this.loading = false
        this.$router.push({name: 'main'})
      })
    },
    // createUser
    createUserOpen () {
      this.createUserModal = true
    },
    createUserClose () {
      this.createUserModal = false
    },
    createUser () {
      this.loading = true
      this.auth.createUser(this.data.email, this.data.password, (data) => {
        this.loading = false
        if (!data) {
          this.validateText = 'すでにメールアドレスが登録されております。'
          return
        }
        this.$router.push({name: 'main'})
      })
    },
    loginOpen () {
      this.loginModal = true
    },
    loginClose () {
      this.loginModal = false
    },
    login () {
      this.loading = true
      this.auth.login(this.data.email, this.data.password, (data) => {
        this.loading = false
        if (!data) {
          this.validateText = 'メールアドレスまたはパスワードが間違っています。'
          return
        }
        this.$router.push({name: 'main'})
      })
    },
    facebookLogin () {
      setTimeout(() => {
        this.loading = true
      }, 1000)
      this.auth.facebookLogin((data) => {
        this.loading = false
        if (!data) {
          this.dialog.alert('ユーザーが存在しません。')
          return
        }
        this.$router.push({name: 'main'})
      })
    }
  }
}
</script>

