  text-align: center;
  font-weight: bold;
  font-size: 16px;
  margin-bottom: 6vh;
}

.facebook-button {
  margin: 0 auto;
  display: block;
  width: 72vw;
  margin-bottom: 20px;
  background: #455797;
  color: white;
  border-radius: 5px;
  font-size: 16px;
  font-weight: bold;
  padding: 8px 0;
  overflow: hidden;
  line-height: 24px;
}

