  opacity: 0.4;
}

.email-button {
  margin: 0 auto;
  display: block;
  width: 72vw;
  margin-bottom: 25px;
  background: #CCCCCC;
  color: black;
  border-radius: 5px;
  font-size: 16px;
  font-weight: bold;
  padding: 8px 0;
  overflow: hidden;
  line-height: 24px;
}

.email-button::before {
  content: '';
  display: block;
  width: 30px;
  height: 25px;
  background: url(../assets/email.svg);
  background-size: 100%;
  background-repeat: no-repeat;
  float: left;
  margin-right: 35px;
  margin-left: 15px;
}

